.layout-main {
  margin-top: -55vh;
  -ms-flex-negative: 0;
  flex-shrink: 0;
}

.layout-container {
  max-width: 1600px;
  width: calc(100% - 16px);
  margin: 0 auto;
}

.layout-content {
  border-radius: 2px;
  padding: 80px 56px;
  margin-bottom: 80px;
}

.layout-layout.is-small-screen .layout-content {
  padding: 40px 28px;
}

.layout-content h3 {
  margin-top: 48px;
}

#view-source {
  position: fixed;
  display: block;
  right: 0;
  bottom: 0;
  margin-right: 40px;
  margin-bottom: 40px;
  z-index: 900;
}

.converter-text-box {
  width: 100px;
}



