.layout-ribbon {
  width: 100%;
  height: 60vh;
  background: #99ccff; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(left top, #3333ff, #ffccff); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(bottom right, #3333ff, #ffccff); /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */
  background: -webkit-gradient(linear, left top, right bottom, from(#3333ff), to(#ffccff));
  background: -webkit-linear-gradient(top left, #3333ff, #ffccff);
  background: -o-linear-gradient(top left, #3333ff, #ffccff);
  background: linear-gradient(to bottom right, #3333ff, #ffccff); /* Standard syntax */
  -ms-flex-negative: 0;
      flex-shrink: 0;
}
.layout-app {
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif !important;
}



